<template>
  <div class="login">
    <!-- 登录 -->
    <p class="sp">elm后台管理系统</p>
    <el-card class="box-card">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        class="demo-ruleForm"
      >
        <el-form-item prop="user_name">
          <el-input v-model.trim="ruleForm.user_name" placeholder="用户名"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input v-model.trim="ruleForm.password" placeholder="密码" type="password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')" :loading="loading">
              {{ loading?'正在登录中' : '立即登录' }}
          </el-button>
        </el-form-item>
      </el-form>
        <div class="trpp">
            <p>温馨提示：</p>
            <p>未登录过的新用户，自动注册</p>
            <p>注册过的用户可凭账号密码登录</p>
        </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        user_name: "",
        password: "",
      },
      loading:false,
      rules: {
        user_name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 2, max: 5, message: "长度在 2 到 5 个字符", trigger: "blur" },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
        ],
      },
    };
  },
  created() {},
  mounted() {
  },
  methods: {
    //   loading加载和登录接口
    submitForm(ruleForm){
        this.$refs[ruleForm].validate((valid)=>{
            if(valid){
                this.loading = true
                this.ajaxLogin()
            }else{
                return false
            }
        })
    },
    //   登录
    async ajaxLogin(){
        let { data:res } = await this.$http.post("/admin/login",this.ruleForm)
        console.log(res)
        if(res.status ==1 ){
           this.$notify.success({
            title:'登录成功',
            type:'success',
            duration:1000,
            position:'top-right'
          });
        }
        this.loading = false
        sessionStorage.setItem('status',res.status)
        this.$router.push('/home')
    }
  },
};
</script>

<style scoped lang="scss">
.login {
  width: 100%;
  height: 100%;
  background: #324057;
  position: relative;
}
.el-card{
    position: absolute;
    width: 400px;
    height: 280px;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    p{
        text-align: center;
        font-size: 11px;
        color: red;
    }
}
.sp{
    text-align: center;
    top: 18%;
    left: 41%;
    position: absolute;
    font-size: 35px;
    color: #fff;
}
.el-button{
    width: 100%;
}
</style>
